.jumbotron {
    position: relative;
    width: 100%;
    height: 400px;
    padding: 0;
    font-family: "Cormorant SC";
    -webkit-box-shadow: 10px 5px 20px 2px lightslategrey;
            box-shadow: 10px 5px 20px 2px lightslategrey;
    margin-bottom: 16px;
}

.jumbotron .overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: auto;
    background: -webkit-gradient(linear, left top, left bottom, from(#333), to(black));
    background: -webkit-linear-gradient(#333, black);
    background: -o-linear-gradient(#333, black);
    background: linear-gradient(#333, black);
    opacity: 0.5;
}

.jumbotron .background-image {
    height: 100%;
    background-image: url("./japanPH.png");
    background-size: cover;
}

.jumbotron .caption {
    position: absolute;
    top: 30%;
    width: 100%;
    text-align: center;
}

.jumbotron .caption h1,
.jumbotron .caption p {
    color: white;
}

.jumbotron h1 {
    font-size: 80px;
    font-weight: 800;
}

.jumbotron p {
    font-size: 30px;
}


